import { Component, Injector, OnInit } from '@angular/core';
import { FormArray, FormControl, FormGroup } from '@angular/forms';
import { FullGSBDonor, FullGSBDonorForm } from 'src/app/model/donor/full-donor-gsb.form';
import { ChangeEvent } from 'src/app/interface/common/change-event';
import { BaseForm } from 'src/app/class/base.form';
import { URLz } from 'src/app/enums/url.enum';

@Component({
  selector: 'donor-gsb',
  templateUrl: './gsb.component.html',
  styleUrls: ['./gsb.component.css'],
  providers: [FullGSBDonorForm],
})
export class GSBComponent extends BaseForm implements OnInit {
  public numbertype = 'number';
  fullGSBDonorForm: FullGSBDonorForm
  constructor(
    public injector: Injector,

  ) {
    super(injector);
    this.fullGSBDonorForm = injector.get(FullGSBDonorForm)
  }
  ngOnInit() {
    this.param.endpoint = URLz.DONOR;
    this._activeId = this._fhs._getURLParam('id');
  }
  patchData() {
    this._http.get({ ...this.param, resource: this._activeId, query: { is_advance: 1 } })
      .subscribe((res: any) => {
        const data: FullGSBDonor = res.data.row;
        this.fillData(data)
      });
  }
  data: FullGSBDonor;
  fillData(datas: any) {
    const data: FullGSBDonor = this.data = datas
    this._fs._form.patchValue({
      donor: data.donor,
      additional_info: data.additional_info,
      gsb: data.gsb,
    });
    const donor = this._fs._form.get('donor') as FormGroup
    donor.addControl('id', new FormControl(this._activeId));
    if (data?.gsb?.part_of_di == 'yes') {
      const event: any = {
        event: { isUserInput: true },
        id: 'yes'
      }
      this.willingChanged(event, data?.gsb)
    }
    if (data?.gsb?.followup == 'yes') {
      this.addFollowupDate(data.gsb.followup_date);
    }
    if (data?.gsb?.amount_purpose == 'individual') {
      this.addIndividual(data.gsb.individual.toString());
    } else if (data?.gsb?.amount_purpose == 'late') {
      const group = this._fs._form.get('gsb') as FormGroup
      group.addControl('late', this._fb.array([this.late()]));
      this.setExistingLate(data.gsb.late);
    }
    // this.willingChanged({id: data?.gsb?.part_of_di})
  }
  get(value: string) {
    return this._fs._form?.get(value)?.value;
  }
  willingChanged(event: ChangeEvent, data) {
    if (event.event.isUserInput) {
      const group = this._fs._form.get('gsb') as FormGroup;
      if (event.id == 'no') {
        group.removeControl('department_id');
        group.removeControl('branch_id');
      } else if (event.id == 'yes') {
        group.addControl(
          'department_id',
          new FormControl(data?.department_id, this._vs._vals('Department'))
        );
        group.addControl(
          'branch_id',
          new FormControl(data?.branch_id, this._vs._vals('Branch'))
        );
      }
    }

  }
  followupChanged(val: any) {
    const group = this._fs._form.get('gsb') as FormGroup;
    if (val.value === 'no') {
      group.removeControl('followup_date');
    } else if (val.value === 'yes') {
      group.addControl(
        'followup_date',
        new FormControl(
          '',
          this._vs._val('Followup Date', {min: 1, max: 31})
        )
      );
    }
  }
  addFollowupDate(data: number) {
    const group = this._fs._form.get('gsb') as FormGroup
    group.addControl(
      'followup_date',
      new FormControl(
        data,
        this._vs._val('Followup Date', { num: 1, max: 31 })
      )
    );
  }
  // Purpose
  purposeChanged(val: ChangeEvent) {
    const grp = this._fs._form.get('gsb') as FormGroup
    if (val.event.isUserInput) {
      if (val.id === 'family') {
        grp.removeControl('late');
        grp.removeControl('individual');
      } else if (val.id === 'individual') {
        grp.removeControl('late');
        this.addIndividual();
      } else if (val.id === 'late') {
        grp.removeControl('individual');
        grp.addControl(
          'late',
          this._fb.array(
            [this.late()],
            [this._vs.repeatTwoFields('name', 'amount')]
          )
        );
        grp.get('late').updateValueAndValidity()
      }
    }
  }
  addIndividual(data = '') {
    const group = this._fs._form.get('gsb') as FormGroup;
    group.addControl(
      'individual',
      new FormControl(
        data,
        this._vs._val('Amount', { max: 1000000000, min: 1, decimal: 1 })
      )
    );
  }
  // 1 Late Initialization
  late(name = '', amount = ''): FormGroup {
    return this._fb.group({
      name: [name, this._vs._val('Name', { minChar: 3, maxChar: 50, alpha: 1 })],
      amount: [
        amount,
        this._vs._val('Amount', { min: 1, max: 1000000000, decimal: 1 }),
      ]
    });
  }
  // 2 Late For Array Iteration Template
  get lates() {
    return this._fs._form?.get('gsb.late') as FormArray;
  }
  // 3 Late Add
  addLate() {
    const arr = this._fs._form?.get('gsb.late') as FormArray;
    arr.push(this.late());
  }
  // 4 Late Remove
  rmvLate(index: number) {
    const arr = this._fs._form?.get('gsb.late') as FormArray;
    arr.removeAt(index);
    arr.updateValueAndValidity();
  }
  // 5 Setting Exsisting items
  setExistingLate(
    lates: {
      name: string;
      amount: string;
    }[]
  ) {
    const arr = this._fs._form?.get('gsb.late') as FormArray;
    if (lates.length > 0) { arr.clear(); }
    lates.forEach((late) => {
      arr.push(this.late(late.name, late.amount));
    });
  }
}
