import {
  AfterViewInit,
  Component,
  Injector,
  Input,
  OnInit,
} from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { debounceTime, distinctUntilChanged } from 'rxjs';
import { BaseForm } from 'src/app/class/base.form';
import { URLz } from 'src/app/enums/url.enum';
import { DonorGSBForm } from 'src/app/model/donor/donor-gsb.form';
import {
  FullBasicDonor,
  FullBasicDonorForm,
} from 'src/app/model/donor/full-basic-donor.form';
import { Transaction } from 'src/app/model/transaction/full.form';
import { GSBComponent } from '../gsb/gsb.component';

@Component({
  selector: 'donor-info',
  templateUrl: './info.component.html',
  styleUrls: ['./info.component.css'],
  providers: [FullBasicDonorForm],
})
export class InfoComponent
  extends BaseForm
  implements OnInit, AfterViewInit
{
  @Input() transDisabled = false;
  @Input() load = true;
  @Input() mobile_search = false;
  params = 'is_active=1';
  fullBasicDonorForm: FullBasicDonorForm
  constructor(
    public injector: Injector,
  ) {
    super(injector);
    this.fullBasicDonorForm = injector.get(FullBasicDonorForm)
  }
  ngOnInit() {
    this.param.endpoint = URLz.DONOR
    this._activeId = this._fhs._getURLParam('id');
  }
  patchData() {
    this._http.get({...this.param, resource: this._activeId}).subscribe((res: any) => {
      const data: FullBasicDonor = res.data.row;
      this.fillData(data);
      const donor = this._fs._form.get('donor') as FormGroup;
      donor.addControl('id', new FormControl(this._activeId));
    });
  }
  fillData(datas: any) {
    const data: FullBasicDonor = datas;
    this._fs._form.patchValue({
      donor: data.donor,
      additional_info: data.additional_info,
    });
  }
  mobileSubscription() {
    const donor = this._fs._form.get('donor') as FormGroup;
    if (!this._activeId) {
      donor
        .get('mobile')
        .valueChanges.pipe(
          debounceTime(450), // Delay between Search Request
          distinctUntilChanged()
        ).subscribe((mobile) => {
          if (mobile != null && mobile != '') {
            this._http
              .get({
                endpoint: this.URLz.DONOR_CHECK,
                query: {
                  mobile,
                  is_advance: this._fhs._getVal('is_advance'),
                },
              })
              .subscribe({
                next: (res: any) => {
                  const data: Transaction = res.data.row;
                  const formz = this._fs._form.get('donor') as FormGroup
                  if(data?.donor?.id){
                    this.patchDonor(data);
                    formz.addControl('id', new FormControl(data?.donor?.id) )
                  }else {
                    formz.removeControl('id')
                    formz.reset();
                    formz.get('mobile').patchValue(mobile)
                    this._fs._form?.get('gsb')?.reset()
                    this._fs._form?.get('additional_info')?.reset()
                  }

                },
              });
          }
        });
    }
  }
  patchDonor(data: Transaction) {
    const donor = this._fs._form.get('donor') as FormGroup;
    // const gsb = this._fs._form.get('gsb') as FormGroup;
    donor?.addControl('id', new FormControl(this._activeId));
    if (data?.donor) {
      if (data?.is_advance === 1 && data?.gsb) {
        const donorGSBForm = new DonorGSBForm(this.injector);
        this._fs._form.addControl(
          'gsb',
          this._fs._fb.group({ ...donorGSBForm.initForm().controls })
        );
        const gsbComponent = new GSBComponent(this.injector);
        gsbComponent.fillData(data);
      } else {
        this.fillData(data);
      }
    }else {
      const mobileCtrl = this._fs._form.get('donor.mobile') as FormControl
      const mobileValue = mobileCtrl.value
      this._fs._form.reset();
      mobileCtrl.patchValue(mobileValue, {emitEvent: false})
    }
    // if (this.gsbComponent && gsb){
    //   gsb.reset()
    //   this._fs._form.removeControl('gsb')
    // }
  }
  ngAfterViewInit(): void {
    this.mobileSubscription();
  }
}
