import { Component, Injector } from '@angular/core';
import { FullBasicDonorForm } from 'src/app/model/donor/full-basic-donor.form';
import { ValidatorService } from 'src/app/service/base.validator.service';
import { FormService } from 'src/app/service/form.service';
import { StateService } from 'src/app/service/state.service';

@Component({
  selector: 'donor-add-info',
  templateUrl: './add-info.component.html',
  styleUrls: ['./add-info.component.css'],
  providers: [FullBasicDonorForm]
})
export class AddInfoComponent {
  _fs: FormService
  _vs: ValidatorService
  _ss: StateService
  constructor(
    public injector: Injector,
    public fullBasicDonorForm: FullBasicDonorForm
  ) {
    this._fs = this.injector.get(FormService)
    this._vs = this.injector.get(ValidatorService)
    this._ss = this.injector.get(StateService)
    // this._fs._form =  this.fullBasicDonorForm.initForm();
  }
}
