import { Component, Injector, OnInit } from '@angular/core';
import { URLz } from 'src/app/enums/url.enum';
import { BaseListClass } from 'src/app/class/base-list-class';
@Component({
  selector: 'app-type-list',
  templateUrl: './type-list.component.html',
  styleUrls: ['./type-list.component.css']
})
export class TypeListComponent extends BaseListClass implements OnInit {
  constructor(injector: Injector) {
    super(injector);
  }
  ngOnInit() {
    this._pathLocation = "donor/type_add";
    this._component = "Donor Type"
    this.initTables();
  }
  initTables(){
    this._reset(URLz.$)
    this._tbls[URLz.$].columns = ['id', 'actions' ,'title', 'description'];
    if (this._ss.checkPermission(this.ACTION.STATUS)) {
      this._tbls[URLz.$].columns.splice(2, 0, 'activate')
    }
    this._tbls[URLz.$].endpoint = URLz.DONOR_T;
    this._formCreator(this._tbls[URLz.$]);
    this._refresh(URLz.$)
  }
}
