import { Component, Injector, OnInit } from '@angular/core';
import { URLz } from 'src/app/enums/url.enum';
import { BaseListClass } from 'src/app/class/base-list-class';
import { range } from 'd3-array';
import { FormGroup } from '@angular/forms';

@Component({
  selector: 'app-advance-setup-list',
  templateUrl: './advance-setup-list.component.html',
  styleUrls: ['./advance-setup-list.component.css'],
})
export class AdvanceSetupListComponent
extends BaseListClass implements OnInit {
  constructor(public injector: Injector) {
    super(injector);
  }
  ngOnInit() {
    this._pathLocation = "/donor/adv_setup_add";
    this._component = "Donor GSB Info"
    this.initTables();
  }
  initTables() {
    this._reset(URLz.$)
    this._tbls[URLz.$].columns = ['id', 'actions' ,'name', 'mobile', 'city_id','area','near_by','collector_id','followup','followup_date']
    ;
    this._tbls[URLz.$].endpoint = URLz.DONOR
    this._tbls[URLz.$].formFilter = this._fb.group({'is_advance': ['1']})
    this._formCreator(this._tbls[URLz.$])
    this._refresh(URLz.$)
    range(1, 31).forEach(x => {
      this.oneToThirty.push({id: x, title: x})
    })
  }
  oneToThirty: {id: number, title: number}[] = [];
  applyClass = { 'col-lg-6': true, 'col-md-6': true };
}
