import { AfterViewInit, Component, Injector, OnInit, ViewChild } from '@angular/core';
import { BaseForm } from 'src/app/class/base.form';
import { URLz } from 'src/app/enums/url.enum';
import { HttpServiceParam } from 'src/app/interface/common/http-service-param';
import { PartialSubmit } from 'src/app/interface/common/partial-submit';
import { FullGSBDonorForm } from 'src/app/model/donor/full-donor-gsb.form';
import { GSBComponent } from '../gsb/gsb.component';

@Component({
  selector: 'di-advance-setup-add',
  templateUrl: './advance-setup-add.component.html',
  styleUrls: ['./advance-setup-add.component.css'],
  providers: [FullGSBDonorForm]
})
export class AdvanceSetupAddComponent extends BaseForm implements OnInit, AfterViewInit{

  constructor(
    public injector: Injector,
    public fullGSBDonorForm: FullGSBDonorForm,
  ) {
    super(injector);
     // Here is the issue
    // Child Component is Overriding the changes made by parent components
    this._fs._form =  this.fullGSBDonorForm.initForm();
    // Getting Id here is mendatory
    this._activeId = this._fhs._getURLParam('id')
  }
  ngOnInit(): void {
    this.param.endpoint = URLz.DONOR;
    this._fhs._pathLocation = '/donor/adv_setup';
  }
  @ViewChild('GSBComponent') gsbComponent:GSBComponent;
  ngAfterViewInit(): void {
    if (this._activeId != null && Number(this._activeId) > 0) {
      this.gsbComponent.patchData();
    }
  }
  _onSubmity(ps: PartialSubmit = this._fss.defaultBehaviour){
    console.log(this._fs._form.value)
    const control = this._fs._form?.get('donor.id')
    if(control?.value) this._activeId = control.value
    return super._onSubmity(ps)
  }
}
