import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ACTION } from 'src/app/enums/action.enum';

import { AdvanceSetupAddComponent } from './advance-setup-add/advance-setup-add.component';
import { AdvanceSetupListComponent } from './advance-setup-list/advance-setup-list.component';
import { BasicAddComponent } from './basic-add/basic-add.component';
import { BasicListComponent } from './basic-list/basic-list.component';
import { TypeAddComponent } from './type-add/type-add.component';
import { TypeListComponent } from './type-list/type-list.component';

export const routes: Routes = [
  {
    path: "",
    children: [
      {
        path: '',
        redirectTo: 'basic',
        pathMatch: 'full'
      },
      {
        path: "basic_add",
        component: BasicAddComponent,

        data: {
          title: "Basic Donor Add",
          permission: [
            ACTION.ADD,
            ACTION.EDIT
          ],
          urls: [
            { title: "Donor"},
            { title: "Basic"},
            { title: "Add" },
          ]
        }
      },
      {
        path: "basic",
        component: BasicListComponent,
        data: {
          title: "Basic Donor",
          permission: [
            ACTION.VIEW
          ],
          urls: [
            { title: "Donor"},
            { title: "Basic"},
            { title: "List" },
          ]
        }
      },
      {
        path: "type_add",
        component: TypeAddComponent,
        data: {
          title: "Donor Type Add",
          permission: [
            ACTION.ADD,
            ACTION.EDIT
          ],
          urls: [
            { title: "Donor"},
            { title: "Type"},
            { title: "Add" },
          ]
        }
      },
      {
        path: "type",
        component: TypeListComponent,
        data: {
          title: "Donor Type",
          permission: [
            ACTION.VIEW
          ],
          urls: [
            { title: "Donor"},
            { title: "Type"},
            { title: "List" },
          ]
        }
      },
      {
        path: "adv_setup_add",
        component: AdvanceSetupAddComponent,
        data: {
          title: "Donor Advance Setup",
          permission: [
            ACTION.ADD,
            ACTION.EDIT
          ],
          urls: [
            { title: "Donor"},
            { title: "Advance Setup"},
            { title: "Add" },
          ]
        }
      },
      {
        path: "adv_setup",
        component: AdvanceSetupListComponent,
        data: {
          title: "Advance Setup",
          permission: [
            ACTION.VIEW
          ],
          urls: [
            { title: "Donor"},
            { title: "Advance Setup"},
            { title: "List" },
          ]
        }
      },
    ]
  }
]

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DonorRoutingModule { }
