import { AfterViewInit, Component, Injector, OnInit, ViewChild } from '@angular/core';
import { BaseForm } from 'src/app/class/base.form';
import { URLz } from 'src/app/enums/url.enum';
import { PartialSubmit } from 'src/app/interface/common/partial-submit';
import { FullBasicDonorForm } from 'src/app/model/donor/full-basic-donor.form';
import { InfoComponent } from '../info/info.component';

@Component({
  selector: 'di-basic-add',
  templateUrl: './basic-add.component.html',
  styleUrls: ['./basic-add.component.css'],
  providers: [FullBasicDonorForm]
})
export class BasicAddComponent extends BaseForm implements OnInit, AfterViewInit {
  constructor(
    public injector: Injector,
    public fullBasicDonorForm: FullBasicDonorForm
  ) {
    super(injector);
    this._fs._form = this.fullBasicDonorForm.initForm();

  }
  ngOnInit(): void {
    this.param.endpoint = URLz.DONOR;
    this._fhs._pathLocation = '/donor/basic';
    this._activeId = this._fhs._getURLParam('id')
  }
  @ViewChild('BasicDonor') basicDonor:InfoComponent;
  ngAfterViewInit(): void {
    if (this._activeId != null && Number(this._activeId) > 0) {
      this.basicDonor.patchData();
    }
  }
  _onSubmity(ps: PartialSubmit = this._fss.defaultBehaviour){
    const control = this._fs._form?.get('donor.id')
    if(control?.value) this._activeId = control.value
    return super._onSubmity(ps)
  }
  resetForm(){
    this._fs._form.reset();
  }
}
