import { Component, Injector, OnInit } from '@angular/core';
import { BaseForm } from 'src/app/class/base.form';
import { URLz } from 'src/app/enums/url.enum';


@Component({
  selector: 'app-type-add',
  templateUrl: './type-add.component.html',
  styleUrls: ['./type-add.component.css'],
})
export class TypeAddComponent extends BaseForm implements OnInit {
  constructor(
    injector: Injector,
  ) {
    super(injector);
    this.param.endpoint = URLz.DONOR_T
  }
  ngOnInit() {
    this._fhs._pathLocation = '/donor/type';
    this.initForm();
    this._activeId = this._fhs._getURLParam('id')
    if (this._activeId != null && Number(this._activeId) > 0) this.patchData();
  }
  initForm() {
    this._fs._form = this._fb.group({
      title: ['', this._vs._val('Donor Type', {minChar: 4,maxChar: 20, specialChar: 1})],
      description: ['', this._vs._val('',{minChar: 1,maxChar: 100,specialChar: 1})],
    });
  }
  patchData() {
    this._http.get({
      endpoint:URLz.DONOR_T,
      resource:+this._activeId
    })
      .subscribe((res: any) => {
      this._fs._form.patchValue({
        title: res.data.row.title,
        description: res.data.row.description,
      });
    });
  }
}
