import { Component, Injector, OnInit } from '@angular/core';
import { BaseListClass } from 'src/app/class/base-list-class';
import { URLz } from 'src/app/enums/url.enum';

@Component({
  selector: 'app-basic-list',
  templateUrl: './basic-list.component.html',
  styleUrls: ['./basic-list.component.css']
})
export class BasicListComponent extends BaseListClass implements OnInit {
  constructor(public injector: Injector) {
    super(injector);
  }
  ngOnInit() {
    this._pathLocation = "/donor/basic_add";
    this._component = "Donor Basic Info"
    // this.initForm();
    this.initTables();


  }
  initTables() {
    this._reset(URLz.$)
    this._tbls[URLz.$].columns = [
      'id',
      'actions',
      'name',
      'mobile',
      'email',
      'donor_type_id',
      'gender',
      'address',
      'country_id',
      'state_id',
      'city_id'];
    this._tbls[URLz.$].endpoint = URLz.DONOR
    this._formCreator(this._tbls[URLz.$])
    this._refresh(URLz.$)
  }
  applyClass = { 'col-lg-6': true, 'col-md-6': true };

}
